import type { Employee } from "@/lib/mock-data"
import { Trophy, Medal, Award } from "lucide-react"

interface LeaderboardTableProps {
  employees: Employee[]
  title: string
}

export function LeaderboardTable({ employees, title }: LeaderboardTableProps) {
  const getRankIcon = (rank: number) => {
    if (rank === 1) return <Trophy className="w-7 h-7 text-[#FFB100]" />
    if (rank === 2) return <Medal className="w-7 h-7 text-[#004EFF]" />
    if (rank === 3) return <Award className="w-7 h-7 text-black" />
    return null
  }

  const getRankColor = (rank: number) => {
    if (rank === 1) return "bg-[#FFB100]/5 border-l-4 border-[#FFB100]"
    if (rank === 2) return "bg-[#004EFF]/5 border-l-4 border-[#004EFF]"
    if (rank === 3) return "bg-gray-100/50 border-l-4 border-gray-800"
    return ""
  }

  return (
    <div className="h-full w-full flex flex-col justify-center p-6 md:p-8 lg:p-12 bg-gray-50">
      <div className="max-w-6xl mx-auto w-full">
        <h1 className="text-4xl md:text-5xl font-bold mb-8 md:mb-12 text-center text-gray-900">{title}</h1>

        <div className="bg-white rounded-xl shadow-sm border border-gray-200 overflow-hidden">
          <div className="divide-y divide-gray-100">
            {employees.map((employee, index) => (
              <div
                key={employee.id}
                className={`flex items-center gap-4 p-4 md:p-6 transition-all duration-200 animate-in fade-in slide-in-from-left hover:bg-gray-50 ${getRankColor(index + 1)}`}
                style={{ animationDelay: `${index * 30}ms` }}
              >
                <div className="flex items-center gap-3 min-w-[100px]">
                  <span className="text-xl md:text-2xl font-bold text-gray-400 w-8 md:w-10 text-right">{index + 1}</span>
                  {getRankIcon(index + 1)}
                </div>

                <div className="w-12 h-12 md:w-14 md:h-14 rounded-full bg-[#004EFF] flex items-center justify-center text-base md:text-lg font-bold text-white shadow-sm">
                  {employee.avatar}
                </div>

                <div className="flex-1 min-w-0">
                  <h3 className="text-lg md:text-xl font-bold text-gray-900 leading-tight truncate">{employee.name}</h3>
                  <p className="text-sm md:text-base text-gray-500 truncate">{employee.department}</p>
                </div>

                <div className="text-right">
                  <div className="text-2xl md:text-3xl font-bold text-gray-900">{employee.dvdScore.toLocaleString()}</div>
                  <p className="text-xs md:text-sm text-gray-500">DVD Score</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  )
}
